.footer {
  height: 200px;
  background-image: url("../images/footer_320x200.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @include onTablet {
    height: 400px;
    background-image: url("../images/footer_744x400.svg");

  }

  @include onDesktop {
    height: 460px;
    background-image: url("../images/footer_1260x460.svg");

  }
}
